.nav{
    position:fixed;
    top:0;
    padding:20px;
    width:100%;
    height:30px;
    z-index:1;

    transition-timing-function:ease-in;
    transition:all 0.5s;
}
.nav__contents{
    display:flex;
    justify-content:space-between
}
.nav__black{
    background-color:#111;
}

.nav__logo{
    position:fixed;
    top:10px;
    left:0;
    width:80px;
    padding-left:20px;
    cursor:pointer;
    object-fit: contain;

}

.nav__avatar{
    position: fixed;
    right:20px;
    cursor:pointer;
    width:30px;
    height:30px;
}