.row{
    color:#fff;
    margin-left:20px;
    margin-top:5px
}

.row h2{
    margin-left:20px;
}
.row__posters{
    display:flex;
    overflow-y:hidden;
    overflow-x:scroll;
    padding:20px;
}
.row__posters::-webkit-scrollbar{
    display:none;
}
.row__poster{
    max-height:160px;
    object-fit: contain;
    margin-right:4px;
    border-radius:5px;
    width:100%;
    transition:transform 450ms;
}

.row__poster:hover{
    transform:scale(1.08);
    opacity:1;
}
.row__posterLarge{
    max-height:450px;
}   
.row__posterLarge:hover{
    transform:scale(1.11);
    opacity:1;
}